import Vue from 'vue'
import Router from 'vue-router'
import IndexPage from '../pages/homePage'
import SoftExamPage from '../pages/softExam'
import WeatherPage from '../pages/weatherPage'
import AboutPage from '../pages/aboutPage'
import MusicPage from '../pages/musicPage'
import SourcePage from '../pages/sourcePage'
import NotFound from '../pages/publicPage/notFound'

Vue.use(Router)

export const RouterMapMobile = [
  {
    path: '/',
    component: IndexPage,
    name: '首页'
  },
  {
    path: '/',
    redirect: '/'
  },
  {
    path: '/softExam',
    component: SoftExamPage,
    name: '信息'
  },
  {
    path: '/music',
    component: MusicPage,
    name: '音乐'
  },
  {
    path: '/source',
    component: SourcePage,
    name: '资源'
  },
  {
    path: '/weather',
    component: WeatherPage,
    name: '天气'
  },
  {
    path: '/about',
    component: AboutPage,
    name: '关于'
  },
  {
    path: '*',
    component: NotFound,
    name: '404'
  }
]
export default new Router({
  // mode: 'history', // 后端支持可开
  routes: RouterMapMobile
})
